.contact-section{
	top: 0;
	transition: 1s;
	
}

.contact-form input, .contact-form textarea{
	width: 100%;
	height: 40px;
	background: rgba(255, 255, 255, 0.2);
	border: 1px solid #fff;
	margin-bottom: 10px;
	border-radius: 5px;
	text-transform: capitalize;
	color: #fff;
	padding: 5px 10px;
}

::placeholder{
	color: #fff;
}

#msg{
	height: 280px;
	resize: none;
	font-family: sans-serif;
}

.form-submit-btn{
	background: #ff4f4f;
	color: #fff;
	text-transform: capitalize;
	padding: 15px 40px;
	display: block;
	margin: auto;
	border: none;
	border-radius: 10px;
	cursor: pointer;
}

.map{
	width: 100%;
	height: 100%;
	padding: 10px;
	border: 2px solid #fff;
	background: rgba(255, 255, 255, 0.2);
	border-radius: 10px;
}

.map iframe{
	width: 100%;
	height: 100%;
	border-radius: 5px;
}

.contact-container {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 50px;
}

.basic-info {
	display: flex;
	justify-content: center;
	align-items: center;
	grid-gap: 0.5rem;
}

.basic-info input {
	width: 20rem!important;
}
