.home-section{
    width: 100%;
    height: 100%;
    padding-left: 50px;
	padding-right: 350px;
    display: flex;
    align-items: center;
    position: relative;
    top: 0;
    transition: 1s ease-in-out;
}

.hero-heading{
    color: #fff;
    font-size: 120px;
    font-weight: 300;
}

.home-img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    object-fit: cover;
    opacity: 0.2;
}