.skill-section{
    position: relative;
}

.top-container{
    margin: auto;
    display: flex;
    grid-gap: 100px;
    color: #fff;
}

.skills-container{
    width: 95%;
    margin: auto;
    display: flex;
    grid-gap: 50px;
    color: #fff;
}

.skill-card{
    position: relative;
}

.skill-img{
    display: block;
    margin: auto;
    height: 200px;
}

.skill-name{
    font-size: 24px;
    /* font-weight: 300; */
    text-align: center;
}

.skill-info{
    text-align: center;
    opacity: 0.5;
    font-size: 18px;
    line-height: 30px;
}

.skill-icon{
    /* position: absolute; */
    width: 128px;
    height: 128px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    border-radius: 50%;
    border: 10px solid;
}

.skill-icon-border{

}

.skill-icon-red, .time-card-red{
    background: #ff4f4f28;
    border-color: #ff4f4f;
    color: #ff4f4f;
}

.skill-icon-blue, .time-card-blue{
    background: #4fa0ff28;
    border-color: #4fa0ff;
    color: #4fa0ff;
}

.skill-icon-yellow, .time-card-yellow{
    background: #ffed4f28;
    border-color: #ffed4f;
    color: #ffed4f;
}

.skill-icon-green, .time-card-green{
    background: #52ff4f28;
    border-color: #52ff4f;
    color: #52ff4f;
}

.skill-icon-cyan, .time-card-cyan{
    background: #4fdfff28;
    border-color: #4fdfff;
    color: #4fdfff;
}

.description{
	font-size: 1.2rem;
    min-width: fit-content;
}

.timeline{
    display: block;
    width: 80%;
}

.timeline .heading{
    margin-bottom: 150px;
}

.vertical-time-card{
    width: 45%;
    padding: 30px;
    border-radius: 10px;
    color: #fff;
    display: block;
    margin: -80px 0;
    position: relative;
    background: #f00;
}

.vertical-time-card:nth-child(even){
    margin-left: auto;
}

.vertical-time-card:nth-child(even):before{
    content: '';
    position: absolute;
    left: -14%;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    border: 5px solid #191919;
    border-radius: 50%;
}

.vertical-time-card:nth-child(even):after{
    content: '';
    position: absolute;
    left: -8.5%;
    top: 50%;
    transform: translateY(-50%);
    width: 7%;
    height: 2px;
    background: #fff;
    z-index: 1;
}

.vertical-time-card:nth-child(odd):before{
    content: '';
    position: absolute;
    right: -14%;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    border: 5px solid #191919;
    border-radius: 50%;
}

.vertical-time-card:nth-child(odd):after{
    content: '';
    position: absolute;
    right: -8.5%;
    top: 50%;
    transform: translateY(-50%);
    width: 7%;
    height: 2px;
    background: #fff;
    z-index: 1;
}

.vertical-time-card:nth-child(2), .vertical-time-card:nth-child(2):before{
    background: #ff4f4f;
}
.vertical-time-card:nth-child(3), .vertical-time-card:nth-child(3):before{
    background: #ffb84f;
}
.vertical-time-card:nth-child(4), .vertical-time-card:nth-child(4):before{
    background: #3dca5c;
}
.vertical-time-card:nth-child(5), .vertical-time-card:nth-child(5):before{
    background: #565252;
}
.vertical-time-card:nth-child(1), .vertical-time-card:nth-child(1):before{
    background: #4fa0ff;
}

.vertical-time-card:nth-child(even) .vertical-time-card-body:before{
    content: '';
    position: absolute;
    left: -11.4%;
    top: 0;
    width: 0px;
    height: 100%;
    border: 1px dashed #fff;
    z-index: 1;
}

.vertical-time-card:nth-child(odd) .vertical-time-card-body:after{
    content: '';
    position: absolute;
    right: -11.4%;
    top: 0;
    width: 0px;
    height: 100%;
    border: 1px dashed #fff;
    z-index: 1;
}

.vertical-time-card-title{
    font-size: 30px;
    font-weight: 300;
    margin-bottom: 20px;
}

.horizontal-time-card {
	width: max-content;
    border-radius: .5rem;
    border-width: 2px;
	transition: .5s;
}

.card-detail {
	font-size: 1.25rem;
}

.timeline-point {
	transform: translateY(-50%);
    width: 24px;
    height: 24px;
    border: 5px solid #0c0d0e;
    border-radius: 50%;
}

.timeline-axis {
	height: 2px;
	border: 1px dashed #fff;
	-webkit-appearance: none;
	appearance: none;
	outline: none;
	background: none;
	transition: 1s .5s;
}

.card-link {
	color: inherit;
}

.card-link:hover {

}

.timeline-axis::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	cursor: pointer;
	width: 24px;
    height: 24px;
	background-color: #565252;
    border: 5px solid #0c0d0e;
    border-radius: 50%;
	transition: 1s .5s;
}

.timeline-axis::-moz-range-thumb {
	cursor: pointer;
	width: 24px;
    height: 24px;
	background-color: #565252;
    border: 5px solid #0c0d0e;
    border-radius: 50%;
	transition: 1s .5s;
}